'use client'

import { useState, useEffect, useCallback } from 'react'

interface TypingStats {
  wpm: number
  cpm: number
  accuracy: number
  totalKeystrokes: number
  correctKeystrokes: number
  isActive: boolean
}

export const useTypingSpeed = () => {
  const [stats, setStats] = useState<TypingStats>({
    wpm: 0,
    cpm: 0,
    accuracy: 100,
    totalKeystrokes: 0,
    correctKeystrokes: 0,
    isActive: false
  })

  const [startTime, setStartTime] = useState<number | null>(null)
  const [keystrokes, setKeystrokes] = useState<number[]>([])
  const [lastKeystroke, setLastKeystroke] = useState<number>(0)

  const resetStats = useCallback(() => {
    setStats({
      wpm: 0,
      cpm: 0,
      accuracy: 100,
      totalKeystrokes: 0,
      correctKeystrokes: 0,
      isActive: false
    })
    setStartTime(null)
    setKeystrokes([])
    setLastKeystroke(0)
  }, [])

  const calculateSpeed = useCallback((keystrokeTimestamps: number[]) => {
    if (keystrokeTimestamps.length < 2) return { wpm: 0, cpm: 0 }

    const now = Date.now()
    const recentKeystrokes = keystrokeTimestamps.filter(
      timestamp => now - timestamp <= 60000 // Last minute
    )

    if (recentKeystrokes.length < 2) return { wpm: 0, cpm: 0 }

    const timeSpan = (now - recentKeystrokes[0]) / 1000 / 60 // in minutes
    const charactersPerMinute = recentKeystrokes.length / timeSpan
    const wordsPerMinute = charactersPerMinute / 5 // Average word length

    return {
      wpm: Math.round(wordsPerMinute),
      cpm: Math.round(charactersPerMinute)
    }
  }, [])

  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      // Ignore special keys and shortcuts
      if (
        event.ctrlKey ||
        event.altKey ||
        event.metaKey ||
        event.key.length > 1 // Ignore special keys like 'Enter', 'Backspace', etc.
      ) {
        return
      }

      const now = Date.now()
      
      if (!startTime) {
        setStartTime(now)
      }

      setLastKeystroke(now)
      
      setKeystrokes(prev => {
        const updated = [...prev, now]
        // Keep only last 5 minutes of keystrokes
        const filtered = updated.filter(timestamp => now - timestamp <= 300000)
        return filtered
      })

      setStats(prev => {
        const newTotalKeystrokes = prev.totalKeystrokes + 1
        const newCorrectKeystrokes = prev.correctKeystrokes + 1 // Simplified - assume all correct
        const accuracy = (newCorrectKeystrokes / newTotalKeystrokes) * 100

        return {
          ...prev,
          totalKeystrokes: newTotalKeystrokes,
          correctKeystrokes: newCorrectKeystrokes,
          accuracy: Math.round(accuracy),
          isActive: true
        }
      })
    }

    document.addEventListener('keydown', handleKeyDown)

    // Update speed calculations every second
    const interval = setInterval(() => {
      setKeystrokes(current => {
        const speed = calculateSpeed(current)
        setStats(prev => ({
          ...prev,
          wpm: speed.wpm,
          cpm: speed.cpm,
          isActive: Date.now() - lastKeystroke < 5000 // Active if typed in last 5 seconds
        }))
        return current
      })
    }, 1000)

    return () => {
      document.removeEventListener('keydown', handleKeyDown)
      clearInterval(interval)
    }
  }, [calculateSpeed, lastKeystroke, startTime])

  return {
    stats,
    resetStats,
    sessionDuration: startTime ? Math.floor((Date.now() - startTime) / 1000) : 0
  }
}
