'use client'

import { useTypingSpeed } from '@/hooks/useTypingSpeed'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'

export default function TypingPage() {
  const { stats, resetStats, sessionDuration } = useTypingSpeed()

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}:${secs.toString().padStart(2, '0')}`
  }

  const getSpeedCategory = (wpm: number) => {
    if (wpm < 20) return { category: 'Beginner', color: 'text-red-500' }
    if (wpm < 40) return { category: 'Average', color: 'text-yellow-500' }
    if (wpm < 60) return { category: 'Good', color: 'text-blue-500' }
    if (wpm < 80) return { category: 'Excellent', color: 'text-green-500' }
    return { category: 'Expert', color: 'text-purple-500' }
  }

  const speedInfo = getSpeedCategory(stats.wpm)

  return (
    <div className="space-y-6 pb-20">
      <div className="text-center space-y-2">
        <h1 className="text-3xl font-bold tracking-tight">Typing Speed Monitor</h1>
        <p className="text-muted-foreground">
          Your typing patterns can indicate stress levels and cognitive load
        </p>
      </div>

      <div className="grid gap-4 md:grid-cols-2">
        {/* Current Speed */}
        <Card>
          <CardHeader>
            <CardTitle>Current Speed</CardTitle>
            <CardDescription>
              {stats.isActive ? 'Actively typing' : 'Start typing to see your speed'}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="text-center">
                <div className="text-4xl font-bold text-primary">
                  {stats.wpm}
                </div>
                <div className="text-sm text-muted-foreground">Words per minute</div>
                <div className={`text-sm font-medium ${speedInfo.color}`}>
                  {speedInfo.category}
                </div>
              </div>
              
              <div className="space-y-2">
                <div className="flex justify-between text-sm">
                  <span>Characters/min:</span>
                  <span className="font-medium">{stats.cpm}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span>Accuracy:</span>
                  <span className="font-medium">{stats.accuracy}%</span>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Session Stats */}
        <Card>
          <CardHeader>
            <CardTitle>Session Statistics</CardTitle>
            <CardDescription>Current typing session data</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="space-y-2">
                <div className="flex justify-between text-sm">
                  <span>Session time:</span>
                  <span className="font-medium">{formatTime(sessionDuration)}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span>Total keystrokes:</span>
                  <span className="font-medium">{stats.totalKeystrokes}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span>Status:</span>
                  <span className={`font-medium ${stats.isActive ? 'text-green-500' : 'text-muted-foreground'}`}>
                    {stats.isActive ? 'Active' : 'Idle'}
                  </span>
                </div>
              </div>
              
              <Button 
                onClick={resetStats} 
                variant="outline" 
                size="sm"
                className="w-full"
              >
                Reset Session
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Accuracy Progress */}
      <Card>
        <CardHeader>
          <CardTitle>Typing Accuracy</CardTitle>
          <CardDescription>Higher accuracy often correlates with lower stress</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-2">
            <div className="flex justify-between text-sm">
              <span>Accuracy Rate</span>
              <span>{stats.accuracy}%</span>
            </div>
            <Progress value={stats.accuracy} className="h-2" />
          </div>
        </CardContent>
      </Card>

      {/* Information Card */}
      <Card>
        <CardHeader>
          <CardTitle>How Typing Speed Relates to Stress</CardTitle>
          <CardDescription>Understanding the connection</CardDescription>
        </CardHeader>
        <CardContent className="space-y-3">
          <div className="space-y-2">
            <h4 className="font-medium">Stress Indicators:</h4>
            <ul className="text-sm text-muted-foreground space-y-1 ml-4">
              <li>• Decreased typing speed during high stress</li>
              <li>• Increased typing errors when anxious</li>
              <li>• Irregular typing rhythm under pressure</li>
              <li>• Faster, more erratic typing when agitated</li>
            </ul>
          </div>
          
          <div className="space-y-2">
            <h4 className="font-medium">Speed Categories:</h4>
            <div className="grid grid-cols-2 gap-2 text-sm">
              <div className="flex justify-between">
                <span>Beginner:</span>
                <span className="text-red-500">0-20 WPM</span>
              </div>
              <div className="flex justify-between">
                <span>Average:</span>
                <span className="text-yellow-500">20-40 WPM</span>
              </div>
              <div className="flex justify-between">
                <span>Good:</span>
                <span className="text-blue-500">40-60 WPM</span>
              </div>
              <div className="flex justify-between">
                <span>Excellent:</span>
                <span className="text-green-500">60-80 WPM</span>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Practice Area */}
      <Card>
        <CardHeader>
          <CardTitle>Practice Area</CardTitle>
          <CardDescription>Type here to test your speed and accuracy</CardDescription>
        </CardHeader>
        <CardContent>
          <textarea
            className="w-full h-32 p-3 border border-border rounded-lg resize-none focus:outline-none focus:ring-2 focus:ring-ring"
            placeholder="Start typing here to measure your speed and accuracy. The system will automatically track your keystrokes and calculate your words per minute..."
          />
          <p className="text-xs text-muted-foreground mt-2">
            Your typing is being monitored automatically. No need to submit or save.
          </p>
        </CardContent>
      </Card>
    </div>
  )
}
